'use client';

import { useState, useEffect, useRef } from 'react';
import { Button } from '@/components/ui/Button';
import { Card } from '@/components/ui/Card';
import { Alert } from '@/components/ui/Alert';
import { InterviewConfiguration } from './InterviewConfig';
import { 
  Mic, 
  MicOff, 
  Video, 
  VideoOff, 
  Play, 
  Pause, 
  Square, 
  SkipForward,
  SkipBack,
  Clock,
  MessageCircle,
  Bot,
  ChevronLeft,
  ChevronRight
} from 'lucide-react';

interface InterviewInterfaceProps {
  config: InterviewConfiguration;
  onEndInterview: (sessionData: InterviewSessionData) => void;
}

export interface InterviewSessionData {
  sessionId: string;
  config: InterviewConfiguration;
  startTime: Date;
  endTime: Date;
  transcript: TranscriptEntry[];
  responses: InterviewResponse[];
  status: 'completed' | 'paused' | 'terminated';
}

interface TranscriptEntry {
  id: string;
  timestamp: Date;
  speaker: 'interviewer' | 'candidate';
  text: string;
  confidence?: number;
}

interface InterviewResponse {
  questionId: string;
  question: string;
  answer: string;
  timestamp: Date;
  duration: number;
  analysis?: {
    confidence: number;
    clarity: number;
    relevance: number;
    structure: number;
  };
}

interface SessionControls {
  isRecording: boolean;
  isPaused: boolean;
  audioEnabled: boolean;
  videoEnabled: boolean;
  timeRemaining: number;
}

export function InterviewInterface({ config, onEndInterview }: InterviewInterfaceProps) {
  const [sessionId] = useState(() => `session_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`);
  const [startTime] = useState(new Date());
  const [currentQuestion, setCurrentQuestion] = useState<string>('');
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [questions, setQuestions] = useState<string[]>([]);
  const [transcript, setTranscript] = useState<TranscriptEntry[]>([]);
  const [responses, setResponses] = useState<InterviewResponse[]>([]);
  const [currentResponse, setCurrentResponse] = useState<string>('');
  const [isListening, setIsListening] = useState(false);
  const [aiSpeaking, setAiSpeaking] = useState(false);
  
  const [controls, setControls] = useState<SessionControls>({
    isRecording: false,
    isPaused: false,
    audioEnabled: true,
    videoEnabled: true,
    timeRemaining: config.duration * 60 // Convert to seconds
  });

  const [connectionStatus, setConnectionStatus] = useState<'connecting' | 'connected' | 'disconnected'>('connecting');
  const [error, setError] = useState<string | null>(null);
  const [cameraReady, setCameraReady] = useState(false);
  const [confidenceScore, setConfidenceScore] = useState<number>(0);

  // Refs for media elements
  const localVideoRef = useRef<HTMLVideoElement>(null);
  const remoteVideoRef = useRef<HTMLVideoElement>(null);
  const mediaStreamRef = useRef<MediaStream | null>(null);
  const peerConnectionRef = useRef<RTCPeerConnection | null>(null);
  const recognitionRef = useRef<any>(null);

  // Timer for countdown
  useEffect(() => {
    if (!controls.isPaused && controls.isRecording && controls.timeRemaining > 0) {
      const timer = setInterval(() => {
        setControls(prev => ({
          ...prev,
          timeRemaining: Math.max(0, prev.timeRemaining - 1)
        }));
      }, 1000);

      return () => clearInterval(timer);
    }
  }, [controls.isPaused, controls.isRecording, controls.timeRemaining]);

  // Auto-end interview when time runs out
  useEffect(() => {
    if (controls.timeRemaining === 0 && controls.isRecording) {
      handleEndInterview();
    }
  }, [controls.timeRemaining, controls.isRecording]);

  // Initialize WebRTC and media
  useEffect(() => {
    const initializeInterview = async () => {
      try {
        // Request camera permission immediately when component mounts
        console.log('Requesting camera access...');
        await initializeMedia();
        initializeSpeechRecognition();
        
        // Generate all questions upfront
        const allQuestions = getQuestionsForConfig(config);
        setQuestions(allQuestions);
        
        // Start confidence analysis after camera is ready
        setTimeout(() => {
          startConfidenceAnalysis();
        }, 1000);
        
        // Simulate AI interviewer starting
        setTimeout(() => {
          setConnectionStatus('connected');
          startInterview();
        }, 2000);
      } catch (error) {
        console.error('Failed to initialize interview:', error);
        setError(`Camera access failed: ${error.message}. Please allow camera permissions and refresh.`);
      }
    };

    initializeInterview();

    return () => {
      cleanup();
    };
  }, []);

  // Cleanup on component unmount to fix camera reinitialization
  useEffect(() => {
    return () => {
      cleanup();
    };
  }, []);

  const initializeMedia = async () => {
    try {
      console.log('Starting media initialization...');
      
      // Cleanup any existing streams first
      if (mediaStreamRef.current) {
        mediaStreamRef.current.getTracks().forEach(track => track.stop());
        mediaStreamRef.current = null;
      }

      // Check if media devices are available
      if (!navigator.mediaDevices || !navigator.mediaDevices.getUserMedia) {
        throw new Error('Media devices not supported in this browser. Please use Chrome, Firefox, or Edge.');
      }

      console.log('Requesting camera and microphone permissions...');

      // Request permissions with detailed error handling
      const constraints = {
        video: {
          width: { ideal: 1280, min: 640 },
          height: { ideal: 720, min: 480 },
          facingMode: 'user'
        },
        audio: {
          echoCancellation: true,
          noiseSuppression: true,
          autoGainControl: true
        }
      };

      const stream = await navigator.mediaDevices.getUserMedia(constraints);
      console.log('Media stream obtained:', stream);
      
      mediaStreamRef.current = stream;
      
      if (localVideoRef.current && stream.getVideoTracks().length > 0) {
        console.log('Setting video source...');
        localVideoRef.current.srcObject = stream;
        localVideoRef.current.muted = true; // Ensure muted to prevent feedback
        
        // Wait for video to load
        await new Promise((resolve, reject) => {
          const video = localVideoRef.current!;
          
          const onLoadedData = () => {
            console.log('Video loaded successfully');
            video.removeEventListener('loadeddata', onLoadedData);
            video.removeEventListener('error', onError);
            setCameraReady(true);
            setError(null);
            resolve(true);
          };
          
          const onError = (e: any) => {
            console.error('Video error:', e);
            video.removeEventListener('loadeddata', onLoadedData);
            video.removeEventListener('error', onError);
            reject(new Error('Failed to load video'));
          };
          
          video.addEventListener('loadeddata', onLoadedData);
          video.addEventListener('error', onError);
          
          // Try to play the video
          video.play().catch(playError => {
            console.log('Autoplay prevented, trying muted play:', playError);
            video.muted = true;
            video.play().catch(e => console.error('Failed to play video:', e));
          });
          
          // Timeout after 10 seconds
          setTimeout(() => {
            if (!video.videoWidth) {
              reject(new Error('Video failed to load within 10 seconds'));
            }
          }, 10000);
        });
      }
      
      // Initialize WebRTC peer connection
      if (peerConnectionRef.current) {
        peerConnectionRef.current.close();
      }
      
      const peerConnection = new RTCPeerConnection({
        iceServers: [
          { urls: 'stun:stun.l.google.com:19302' },
          { urls: 'stun:stun1.l.google.com:19302' }
        ]
      });
      
      peerConnectionRef.current = peerConnection;
      
      // Add local stream to peer connection
      stream.getTracks().forEach(track => {
        peerConnection.addTrack(track, stream);
      });
      
      // Handle remote stream
      peerConnection.ontrack = (event) => {
        if (remoteVideoRef.current) {
          remoteVideoRef.current.srcObject = event.streams[0];
        }
      };

      // Monitor device changes
      navigator.mediaDevices.addEventListener('devicechange', handleDeviceChange);
      
      console.log('Media initialization completed successfully');
      
    } catch (err) {
      console.error('Error accessing media devices:', err);
      setCameraReady(false);
      
      // Provide specific error messages based on error type
      let errorMessage = 'Unable to access camera or microphone.';
      let troubleshooting: string[] = [];

      if (err instanceof Error) {
        if (err.name === 'NotAllowedError' || err.name === 'PermissionDeniedError') {
          errorMessage = 'Camera and microphone access denied.';
          troubleshooting = [
            '1. Click the camera icon in your browser\'s address bar',
            '2. Select "Allow" for camera and microphone permissions',
            '3. Refresh the page and try again',
            '4. Check your browser settings for site permissions'
          ];
        } else if (err.name === 'NotFoundError' || err.name === 'DevicesNotFoundError') {
          errorMessage = 'No camera or microphone found.';
          troubleshooting = [
            '1. Check that your camera and microphone are connected',
            '2. Try unplugging and reconnecting your devices',
            '3. Check if other applications are using your camera/microphone',
            '4. Restart your browser and try again'
          ];
        } else if (err.name === 'NotReadableError' || err.name === 'TrackStartError') {
          errorMessage = 'Camera or microphone is already in use.';
          troubleshooting = [
            '1. Close other applications that might be using your camera/microphone',
            '2. Close other browser tabs with video calls',
            '3. Restart your browser',
            '4. Restart your computer if the issue persists'
          ];
        } else if (err.name === 'OverconstrainedError' || err.name === 'ConstraintNotSatisfiedError') {
          errorMessage = 'Camera or microphone settings not supported.';
          troubleshooting = [
            '1. Try using a different camera or microphone',
            '2. Update your device drivers',
            '3. Check your camera resolution settings',
            '4. Try refreshing the page'
          ];
        }
      }

      setError(`${errorMessage}\n\nTroubleshooting steps:\n${troubleshooting.join('\n')}`);
      throw err;
    }
  };

  // Simple confidence analysis based on video
  const startConfidenceAnalysis = () => {
    const analyzeConfidence = () => {
      if (!localVideoRef.current || !cameraReady) {
        // Fallback confidence when camera isn't ready
        setConfidenceScore(Math.round(50 + Math.random() * 30));
        return;
      }

      // Simple confidence analysis based on video activity
      const video = localVideoRef.current;
      
      if (video.videoWidth > 0 && video.videoHeight > 0) {
        // Create a canvas to analyze video frames
        const canvas = document.createElement('canvas');
        const ctx = canvas.getContext('2d');
        
        if (ctx) {
          canvas.width = video.videoWidth;
          canvas.height = video.videoHeight;
          
          try {
            ctx.drawImage(video, 0, 0);
            const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
            
            // Simple brightness analysis as confidence indicator
            let brightness = 0;
            for (let i = 0; i < imageData.data.length; i += 4) {
              brightness += (imageData.data[i] + imageData.data[i + 1] + imageData.data[i + 2]) / 3;
            }
            brightness = brightness / (imageData.data.length / 4);
            
            // Convert brightness to confidence score (0-100)
            const confidence = Math.min(100, Math.max(20, (brightness / 255) * 100 + Math.random() * 20));
            setConfidenceScore(Math.round(confidence));
          } catch (error) {
            // Fallback to random confidence for demo
            setConfidenceScore(Math.round(60 + Math.random() * 30));
          }
        }
      } else {
        // Video not ready, use fallback
        setConfidenceScore(Math.round(50 + Math.random() * 30));
      }
    };

    // Analyze confidence every 2 seconds
    const interval = setInterval(analyzeConfidence, 2000);
    return () => clearInterval(interval);
  };

  const handleDeviceChange = async () => {
    try {
      // Re-enumerate devices
      const devices = await navigator.mediaDevices.enumerateDevices();
      const videoDevices = devices.filter(device => device.kind === 'videoinput');
      const audioDevices = devices.filter(device => device.kind === 'audioinput');

      // Check if current devices are still available
      if (mediaStreamRef.current) {
        const videoTrack = mediaStreamRef.current.getVideoTracks()[0];
        const audioTrack = mediaStreamRef.current.getAudioTracks()[0];

        // If devices were disconnected, try to get new ones
        if (videoTrack && videoTrack.readyState === 'ended' && videoDevices.length > 0) {
          await reinitializeVideo();
        }
        if (audioTrack && audioTrack.readyState === 'ended' && audioDevices.length > 0) {
          await reinitializeAudio();
        }
      }
    } catch (err) {
      console.error('Error handling device change:', err);
    }
  };

  const reinitializeVideo = async () => {
    try {
      const videoStream = await navigator.mediaDevices.getUserMedia({ 
        video: { 
          width: { ideal: 1280 }, 
          height: { ideal: 720 }, 
          facingMode: 'user' 
        } 
      });
      
      if (localVideoRef.current) {
        localVideoRef.current.srcObject = videoStream;
      }

      // Replace video track in peer connection
      if (peerConnectionRef.current && mediaStreamRef.current) {
        const videoTrack = videoStream.getVideoTracks()[0];
        const sender = peerConnectionRef.current.getSenders().find(s => 
          s.track && s.track.kind === 'video'
        );
        if (sender) {
          await sender.replaceTrack(videoTrack);
        }
        
        // Update media stream reference
        const audioTrack = mediaStreamRef.current.getAudioTracks()[0];
        mediaStreamRef.current = new MediaStream([videoTrack, audioTrack].filter(Boolean));
      }
    } catch (err) {
      console.error('Error reinitializing video:', err);
    }
  };

  const reinitializeAudio = async () => {
    try {
      const audioStream = await navigator.mediaDevices.getUserMedia({ 
        audio: {
          echoCancellation: true,
          noiseSuppression: true,
          autoGainControl: true
        }
      });
      
      // Replace audio track in peer connection
      if (peerConnectionRef.current && mediaStreamRef.current) {
        const audioTrack = audioStream.getAudioTracks()[0];
        const sender = peerConnectionRef.current.getSenders().find(s => 
          s.track && s.track.kind === 'audio'
        );
        if (sender) {
          await sender.replaceTrack(audioTrack);
        }
        
        // Update media stream reference
        const videoTrack = mediaStreamRef.current.getVideoTracks()[0];
        mediaStreamRef.current = new MediaStream([videoTrack, audioTrack].filter(Boolean));
      }
    } catch (err) {
      console.error('Error reinitializing audio:', err);
    }
  };

  const initializeSpeechRecognition = () => {
    if ('webkitSpeechRecognition' in window || 'SpeechRecognition' in window) {
      const SpeechRecognition = (window as any).webkitSpeechRecognition || (window as any).SpeechRecognition;
      const recognition = new SpeechRecognition();
      
      // Enhanced configuration for better accuracy
      recognition.continuous = true;
      recognition.interimResults = true;
      recognition.lang = 'en-US';
      recognition.maxAlternatives = 3;
      
      // Add grammar hints for better recognition
      if ('webkitSpeechGrammarList' in window) {
        const SpeechGrammarList = (window as any).webkitSpeechGrammarList;
        const grammarList = new SpeechGrammarList();
        
        // Add common interview terms
        const grammar = '#JSGF V1.0; grammar interview; public <interview> = ' +
          'experience | project | challenge | team | leadership | technical | ' +
          'algorithm | database | framework | API | testing | deployment | ' +
          'agile | scrum | javascript | python | react | node | aws | docker;';
        
        grammarList.addFromString(grammar, 1);
        recognition.grammars = grammarList;
      }
      
      let finalTranscriptBuffer = '';
      let lastResultTime = Date.now();
      
      recognition.onresult = (event: any) => {
        let interimTranscript = '';
        let finalTranscript = '';
        
        for (let i = event.resultIndex; i < event.results.length; i++) {
          const result = event.results[i];
          const transcript = result[0].transcript;
          const confidence = result[0].confidence;
          
          if (result.isFinal) {
            finalTranscript += transcript;
            finalTranscriptBuffer += transcript;
            
            // Only add to transcript if confidence is reasonable
            if (confidence > 0.6) {
              addTranscriptEntry('candidate', transcript, confidence);
              setCurrentResponse(prev => prev + transcript);
            } else {
              // Low confidence - ask for clarification
              console.log('Low confidence transcription:', transcript, confidence);
              addTranscriptEntry('candidate', `[Unclear: "${transcript}"]`, confidence);
            }
          } else {
            interimTranscript += transcript;
          }
        }
        
        lastResultTime = Date.now();
        
        // Show interim results for real-time feedback
        if (interimTranscript) {
          setCurrentResponse(finalTranscriptBuffer + interimTranscript);
        }
      };
      
      recognition.onstart = () => {
        console.log('Speech recognition started');
        setIsListening(true);
      };
      
      recognition.onend = () => {
        console.log('Speech recognition ended');
        setIsListening(false);
        
        // Auto-restart if we're still recording and it wasn't manually stopped
        if (controls.isRecording && !controls.isPaused) {
          setTimeout(() => {
            try {
              recognition.start();
            } catch (err) {
              console.log('Recognition restart failed:', err);
            }
          }, 100);
        }
      };
      
      recognition.onerror = (event: any) => {
        console.error('Speech recognition error:', event.error);
        
        let errorMessage = 'Speech recognition error occurred.';
        let shouldRestart = true;
        
        switch (event.error) {
          case 'no-speech':
            errorMessage = 'No speech detected. Please speak clearly into your microphone.';
            break;
          case 'audio-capture':
            errorMessage = 'Microphone not accessible. Please check your microphone connection.';
            shouldRestart = false;
            break;
          case 'not-allowed':
            errorMessage = 'Microphone permission denied. Please allow microphone access.';
            shouldRestart = false;
            break;
          case 'network':
            errorMessage = 'Network error occurred. Trying to continue with offline recognition.';
            break;
          case 'service-not-allowed':
            errorMessage = 'Speech recognition service not available.';
            shouldRestart = false;
            break;
          case 'bad-grammar':
            errorMessage = 'Speech recognition grammar error.';
            break;
          case 'language-not-supported':
            errorMessage = 'Language not supported. Switching to default language.';
            recognition.lang = 'en-US';
            break;
        }
        
        // Show error to user if it's critical
        if (!shouldRestart) {
          setError(errorMessage);
        }
        
        // Auto-restart for recoverable errors
        if (shouldRestart && controls.isRecording && !controls.isPaused) {
          setTimeout(() => {
            try {
              recognition.start();
            } catch (err) {
              console.log('Recognition restart after error failed:', err);
            }
          }, 1000);
        }
      };
      
      // Monitor audio levels for quality feedback
      recognition.onaudiostart = () => {
        console.log('Audio capture started');
      };
      
      recognition.onaudioend = () => {
        console.log('Audio capture ended');
      };
      
      recognition.onspeechstart = () => {
        console.log('Speech detected');
      };
      
      recognition.onspeechend = () => {
        console.log('Speech ended');
      };
      
      recognitionRef.current = recognition;
    } else {
      setError('Speech recognition not supported in this browser. Please use Chrome, Edge, or Safari.');
    }
  };

  const startInterview = () => {
    setControls(prev => ({ ...prev, isRecording: true }));
    
    // Start speech recognition
    if (recognitionRef.current) {
      recognitionRef.current.start();
      setIsListening(true);
    }
    
    // Generate first question
    generateNextQuestion();
  };

  const generateNextQuestion = () => {
    if (currentQuestionIndex < questions.length) {
      const nextQuestion = questions[currentQuestionIndex];
      setCurrentQuestion(nextQuestion);
      addTranscriptEntry('interviewer', nextQuestion);
      
      // Simulate AI speaking
      setAiSpeaking(true);
      setTimeout(() => {
        setAiSpeaking(false);
      }, 3000);
    } else {
      setCurrentQuestion("Thank you for your responses. Do you have any questions for me?");
      addTranscriptEntry('interviewer', "Thank you for your responses. Do you have any questions for me?");
    }
  };

  const handleNextQuestion = () => {
    // Save current response if any
    if (currentResponse.trim()) {
      const response: InterviewResponse = {
        questionId: `q_${currentQuestionIndex + 1}`,
        question: currentQuestion,
        answer: currentResponse.trim(),
        timestamp: new Date(),
        duration: 0,
        analysis: {
          confidence: 0.8,
          clarity: 0.7,
          relevance: 0.9,
          structure: 0.6
        }
      };
      
      setResponses(prev => [...prev, response]);
      setCurrentResponse('');
    }
    
    // Move to next question
    if (currentQuestionIndex < questions.length - 1) {
      setCurrentQuestionIndex(prev => prev + 1);
      setTimeout(() => generateNextQuestion(), 500);
    }
  };

  const handlePreviousQuestion = () => {
    if (currentQuestionIndex > 0) {
      setCurrentQuestionIndex(prev => prev - 1);
      const prevQuestion = questions[currentQuestionIndex - 1];
      setCurrentQuestion(prevQuestion);
      addTranscriptEntry('interviewer', `Returning to: ${prevQuestion}`);
      
      // Load previous response if exists
      const prevResponse = responses[currentQuestionIndex - 1];
      if (prevResponse) {
        setCurrentResponse(prevResponse.answer);
      }
    }
  };

  const getQuestionsForConfig = (config: InterviewConfiguration): string[] => {
    const baseQuestions = [
      `Tell me about yourself and why you're interested in the ${config.role} position at ${config.company}.`,
      "What are your greatest strengths and how do they relate to this role?",
      "Describe a challenging project you've worked on recently.",
      "How do you handle working under pressure or tight deadlines?",
      "Where do you see yourself in 5 years?"
    ];
    
    if (config.interviewType === 'technical' || config.interviewType === 'mixed') {
      baseQuestions.push(
        "Walk me through how you would approach solving a complex technical problem.",
        "Explain a technical concept you're passionate about to someone non-technical.",
        "How do you stay updated with the latest technologies in your field?"
      );
    }
    
    if (config.focusAreas.includes('System Design')) {
      baseQuestions.push(
        "Design a system that can handle millions of users. Walk me through your approach."
      );
    }
    
    return baseQuestions;
  };

  const addTranscriptEntry = (speaker: 'interviewer' | 'candidate', text: string, confidence?: number) => {
    const entry: TranscriptEntry = {
      id: `${Date.now()}_${Math.random().toString(36).substr(2, 9)}`,
      timestamp: new Date(),
      speaker,
      text,
      confidence
    };
    
    setTranscript(prev => [...prev, entry]);
  };

  const handlePauseResume = () => {
    setControls(prev => ({ ...prev, isPaused: !prev.isPaused }));
    
    if (recognitionRef.current) {
      if (controls.isPaused) {
        recognitionRef.current.start();
        setIsListening(true);
      } else {
        recognitionRef.current.stop();
        setIsListening(false);
      }
    }
  };

  const handleSkipQuestion = () => {
    handleNextQuestion();
  };

  const handleEndInterview = () => {
    // Save final response if any
    if (currentResponse.trim()) {
      const response: InterviewResponse = {
        questionId: `q_${responses.length + 1}`,
        question: currentQuestion,
        answer: currentResponse.trim(),
        timestamp: new Date(),
        duration: 0,
        analysis: {
          confidence: 0.8,
          clarity: 0.7,
          relevance: 0.9,
          structure: 0.6
        }
      };
      
      setResponses(prev => [...prev, response]);
    }
    
    const sessionData: InterviewSessionData = {
      sessionId,
      config,
      startTime,
      endTime: new Date(),
      transcript,
      responses: [...responses],
      status: 'completed'
    };
    
    cleanup();
    onEndInterview(sessionData);
  };

  const toggleAudio = () => {
    setControls(prev => ({ ...prev, audioEnabled: !prev.audioEnabled }));
    
    if (mediaStreamRef.current) {
      const audioTracks = mediaStreamRef.current.getAudioTracks();
      audioTracks.forEach(track => {
        track.enabled = !controls.audioEnabled;
      });
    }
  };

  const toggleVideo = () => {
    setControls(prev => ({ ...prev, videoEnabled: !prev.videoEnabled }));
    
    if (mediaStreamRef.current) {
      const videoTracks = mediaStreamRef.current.getVideoTracks();
      videoTracks.forEach(track => {
        track.enabled = !controls.videoEnabled;
      });
    }
  };

  const cleanup = () => {
    if (recognitionRef.current) {
      try {
        recognitionRef.current.stop();
      } catch (e) {
        console.log('Recognition already stopped');
      }
    }
    
    if (mediaStreamRef.current) {
      mediaStreamRef.current.getTracks().forEach(track => {
        track.stop();
      });
      mediaStreamRef.current = null;
    }
    
    if (peerConnectionRef.current) {
      peerConnectionRef.current.close();
      peerConnectionRef.current = null;
    }
    
    // Remove event listeners
    if (navigator.mediaDevices) {
      navigator.mediaDevices.removeEventListener('devicechange', handleDeviceChange);
    }
    
    // Clear video elements
    if (localVideoRef.current) {
      localVideoRef.current.srcObject = null;
    }
    if (remoteVideoRef.current) {
      remoteVideoRef.current.srcObject = null;
    }
    
    setIsListening(false);
  };

  const formatTime = (seconds: number): string => {
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins}:${secs.toString().padStart(2, '0')}`;
  };

  if (connectionStatus === 'connecting') {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <Card className="p-8 text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto mb-4"></div>
          <h2 className="text-xl font-semibold mb-2">Connecting to AI Interviewer</h2>
          <p className="text-gray-600">Setting up your interview session...</p>
        </Card>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50 p-4">
      {error && (
        <Alert variant="error" className="mb-4">
          <div className="space-y-2">
            <p>{error}</p>
            <Button 
              size="sm" 
              onClick={() => {
                setError(null);
                initializeMedia();
              }}
            >
              Try Again
            </Button>
          </div>
        </Alert>
      )}
      
      <div className="max-w-7xl mx-auto grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* Video Section */}
        <div className="lg:col-span-2 space-y-4">
          {/* AI Interviewer Video */}
          <Card className="p-4">
            <div className="aspect-video bg-gradient-to-br from-blue-900 to-purple-900 rounded-lg relative overflow-hidden">
              {/* AI Avatar */}
              <div className="absolute inset-0 flex items-center justify-center">
                <div className={`w-32 h-32 rounded-full bg-gradient-to-br from-blue-400 to-purple-500 flex items-center justify-center transition-all duration-300 ${aiSpeaking ? 'scale-110 shadow-lg shadow-blue-500/50' : 'scale-100'}`}>
                  <Bot className={`w-16 h-16 text-white transition-all duration-300 ${aiSpeaking ? 'animate-pulse' : ''}`} />
                </div>
              </div>
              
              {/* AI Status Indicator */}
              <div className="absolute top-4 left-4 flex items-center space-x-2">
                <div className={`w-3 h-3 rounded-full ${connectionStatus === 'connected' ? 'bg-green-400' : 'bg-yellow-400'} ${aiSpeaking ? 'animate-pulse' : ''}`}></div>
                <span className="text-white text-sm font-medium">
                  {aiSpeaking ? 'AI Speaking...' : 'AI Interviewer'}
                </span>
              </div>
              
              {/* Question Progress */}
              <div className="absolute bottom-4 left-4 right-4">
                <div className="bg-black bg-opacity-50 text-white px-3 py-2 rounded-lg">
                  <div className="flex items-center justify-between mb-2">
                    <span className="text-sm">Question {currentQuestionIndex + 1} of {questions.length}</span>
                    <div className="flex space-x-1">
                      <Button
                        size="sm"
                        variant="secondary"
                        onClick={handlePreviousQuestion}
                        disabled={currentQuestionIndex === 0 || controls.isPaused}
                        className="p-1 h-6 w-6"
                      >
                        <ChevronLeft className="w-3 h-3" />
                      </Button>
                      <Button
                        size="sm"
                        variant="secondary"
                        onClick={handleNextQuestion}
                        disabled={currentQuestionIndex >= questions.length - 1 || controls.isPaused}
                        className="p-1 h-6 w-6"
                      >
                        <ChevronRight className="w-3 h-3" />
                      </Button>
                    </div>
                  </div>
                  <div className="w-full bg-gray-600 rounded-full h-1">
                    <div 
                      className="bg-blue-400 h-1 rounded-full transition-all duration-300" 
                      style={{ width: `${((currentQuestionIndex + 1) / questions.length) * 100}%` }}
                    ></div>
                  </div>
                </div>
              </div>
              
              {/* Hidden video element for future WebRTC */}
              <video
                ref={remoteVideoRef}
                autoPlay
                playsInline
                className="hidden w-full h-full object-cover"
              />
            </div>
          </Card>
          
          {/* Candidate Video */}
          <Card className="p-4">
            <div className="aspect-video bg-gray-900 rounded-lg relative overflow-hidden">
              <video
                ref={localVideoRef}
                autoPlay
                playsInline
                muted
                className="w-full h-full object-cover"
              />
              <div className="absolute bottom-4 left-4 bg-black bg-opacity-50 text-white px-3 py-1 rounded">
                You {cameraReady ? '✓' : '⚠️'}
              </div>
              
              {/* Confidence Score */}
              <div className="absolute top-4 right-4 bg-black bg-opacity-50 text-white px-3 py-2 rounded-lg">
                <div className="text-xs text-gray-300">Confidence</div>
                <div className={`text-lg font-bold ${
                  confidenceScore >= 80 ? 'text-green-400' : 
                  confidenceScore >= 60 ? 'text-yellow-400' : 'text-red-400'
                }`}>
                  {confidenceScore}%
                </div>
              </div>
              
              {isListening && (
                <div className="absolute top-4 left-4 bg-red-500 text-white px-2 py-1 rounded-full text-sm flex items-center">
                  <div className="w-2 h-2 bg-white rounded-full mr-2 animate-pulse"></div>
                  Listening
                </div>
              )}
              
              {!cameraReady && (
                <div className="absolute inset-0 flex items-center justify-center bg-gray-800 bg-opacity-75">
                  <div className="text-center text-white">
                    <Video className="w-12 h-12 mx-auto mb-4 opacity-50" />
                    <p className="text-sm mb-4">Camera not ready</p>
                    <Button 
                      size="sm" 
                      onClick={async () => {
                        try {
                          await initializeMedia();
                        } catch (e) {
                          console.error('Manual camera init failed:', e);
                        }
                      }}
                      className="bg-blue-600 hover:bg-blue-700"
                    >
                      Enable Camera
                    </Button>
                  </div>
                </div>
              )}
            </div>
          </Card>
        </div>
        
        {/* Control Panel */}
        <div className="space-y-4">
          {/* Session Info */}
          <Card className="p-4">
            <div className="flex items-center justify-between mb-4">
              <h3 className="font-semibold">Interview Session</h3>
              <div className="flex items-center text-sm text-gray-600">
                <Clock className="w-4 h-4 mr-1" />
                {formatTime(controls.timeRemaining)}
              </div>
            </div>
            
            <div className="space-y-2 text-sm">
              <div><span className="font-medium">Role:</span> {config.role}</div>
              <div><span className="font-medium">Company:</span> {config.company}</div>
              <div><span className="font-medium">Type:</span> {config.interviewType}</div>
              <div><span className="font-medium">Difficulty:</span> {config.difficulty}</div>
            </div>
          </Card>
          
          {/* Session Controls */}
          <Card className="p-4">
            <h3 className="font-semibold mb-4">Controls</h3>
            
            <div className="grid grid-cols-2 gap-2 mb-4">
              <Button
                variant={controls.audioEnabled ? "primary" : "secondary"}
                onClick={toggleAudio}
                className="flex items-center justify-center"
              >
                {controls.audioEnabled ? <Mic className="w-4 h-4" /> : <MicOff className="w-4 h-4" />}
              </Button>
              
              <Button
                variant={controls.videoEnabled ? "primary" : "secondary"}
                onClick={toggleVideo}
                className="flex items-center justify-center"
              >
                {controls.videoEnabled ? <Video className="w-4 h-4" /> : <VideoOff className="w-4 h-4" />}
              </Button>
            </div>
            
            <div className="space-y-2">
              <Button
                variant="secondary"
                onClick={handlePauseResume}
                className="w-full flex items-center justify-center"
                disabled={!controls.isRecording}
              >
                {controls.isPaused ? <Play className="w-4 h-4 mr-2" /> : <Pause className="w-4 h-4 mr-2" />}
                {controls.isPaused ? 'Resume' : 'Pause'}
              </Button>
              
              <div className="grid grid-cols-2 gap-2">
                <Button
                  variant="secondary"
                  onClick={handlePreviousQuestion}
                  className="flex items-center justify-center"
                  disabled={currentQuestionIndex === 0 || controls.isPaused}
                >
                  <SkipBack className="w-4 h-4 mr-1" />
                  Previous
                </Button>
                
                <Button
                  variant="secondary"
                  onClick={handleNextQuestion}
                  className="flex items-center justify-center"
                  disabled={currentQuestionIndex >= questions.length - 1 || controls.isPaused}
                >
                  Next
                  <SkipForward className="w-4 h-4 ml-1" />
                </Button>
              </div>
              
              <Button
                variant="danger"
                onClick={handleEndInterview}
                className="w-full flex items-center justify-center"
              >
                <Square className="w-4 h-4 mr-2" />
                End Interview
              </Button>
            </div>
          </Card>
          
          {/* Current Question */}
          <Card className="p-4">
            <h3 className="font-semibold mb-2 flex items-center">
              <MessageCircle className="w-4 h-4 mr-2" />
              Current Question
            </h3>
            <p className="text-sm text-gray-700 bg-gray-50 p-3 rounded">
              {currentQuestion || "Waiting for next question..."}
            </p>
          </Card>

          {/* Confidence Analysis */}
          <Card className="p-4">
            <h3 className="font-semibold mb-2">Live Analysis</h3>
            <div className="space-y-3">
              <div>
                <div className="flex justify-between text-sm mb-1">
                  <span>Confidence</span>
                  <span className={`font-medium ${
                    confidenceScore >= 80 ? 'text-green-600' : 
                    confidenceScore >= 60 ? 'text-yellow-600' : 'text-red-600'
                  }`}>
                    {confidenceScore}%
                  </span>
                </div>
                <div className="w-full bg-gray-200 rounded-full h-2">
                  <div 
                    className={`h-2 rounded-full transition-all duration-500 ${
                      confidenceScore >= 80 ? 'bg-green-500' : 
                      confidenceScore >= 60 ? 'bg-yellow-500' : 'bg-red-500'
                    }`}
                    style={{ width: `${confidenceScore}%` }}
                  ></div>
                </div>
              </div>
              
              <div className="text-xs text-gray-500">
                {cameraReady ? '📹 Camera active' : '⚠️ Camera initializing'}
                {isListening && ' • 🎤 Listening'}
              </div>
            </div>
          </Card>
          
          {/* Live Transcript */}
          <Card className="p-4">
            <h3 className="font-semibold mb-2">Live Transcript</h3>
            <div className="max-h-40 overflow-y-auto space-y-2 text-sm">
              {transcript.slice(-5).map(entry => (
                <div
                  key={entry.id}
                  className={`p-2 rounded ${
                    entry.speaker === 'interviewer' 
                      ? 'bg-blue-50 border-l-2 border-blue-500' 
                      : 'bg-green-50 border-l-2 border-green-500'
                  }`}
                >
                  <div className="font-medium text-xs text-gray-500 mb-1">
                    {entry.speaker === 'interviewer' ? 'AI Interviewer' : 'You'} • {entry.timestamp.toLocaleTimeString()}
                  </div>
                  <div>{entry.text}</div>
                </div>
              ))}
              {currentResponse && (
                <div className="p-2 rounded bg-yellow-50 border-l-2 border-yellow-500">
                  <div className="font-medium text-xs text-gray-500 mb-1">You • Speaking...</div>
                  <div>{currentResponse}</div>
                </div>
              )}
            </div>
          </Card>
        </div>
      </div>
    </div>
  );
}