'use client';

import { useState } from 'react';
import { Button } from '@/components/ui/Button';
import { Input } from '@/components/ui/Input';
import { Card } from '@/components/ui/Card';
import { Alert } from '@/components/ui/Alert';

interface InterviewConfigProps {
  onStartInterview: (config: InterviewConfiguration) => void;
  loading?: boolean;
}

export interface InterviewConfiguration {
  role: string;
  company: string;
  duration: number; // in minutes
  difficulty: 'beginner' | 'intermediate' | 'advanced';
  interviewType: 'behavioral' | 'technical' | 'mixed';
  focusAreas: string[];
  aiPersonality: 'friendly' | 'professional' | 'challenging';
}

const FOCUS_AREAS = [
  'Problem Solving',
  'Communication',
  'Leadership',
  'Technical Skills',
  'System Design',
  'Algorithms',
  'Data Structures',
  'Team Collaboration',
  'Project Management',
  'Cultural Fit'
];

const INTERVIEW_TEMPLATES = [
  {
    name: 'Software Engineer - Entry Level',
    role: 'Software Engineer',
    company: 'Tech Company',
    duration: 45,
    difficulty: 'beginner' as const,
    interviewType: 'mixed' as const,
    focusAreas: ['Technical Skills', 'Problem Solving', 'Communication'],
    aiPersonality: 'friendly' as const
  },
  {
    name: 'Senior Developer - Technical',
    role: 'Senior Software Developer',
    company: 'Startup',
    duration: 60,
    difficulty: 'advanced' as const,
    interviewType: 'technical' as const,
    focusAreas: ['System Design', 'Algorithms', 'Leadership'],
    aiPersonality: 'challenging' as const
  },
  {
    name: 'Product Manager - Behavioral',
    role: 'Product Manager',
    company: 'Enterprise',
    duration: 50,
    difficulty: 'intermediate' as const,
    interviewType: 'behavioral' as const,
    focusAreas: ['Leadership', 'Communication', 'Project Management'],
    aiPersonality: 'professional' as const
  }
];

export function InterviewConfig({ onStartInterview, loading = false }: InterviewConfigProps) {
  const [config, setConfig] = useState<InterviewConfiguration>({
    role: '',
    company: '',
    duration: 45,
    difficulty: 'intermediate',
    interviewType: 'mixed',
    focusAreas: [],
    aiPersonality: 'professional'
  });
  
  const [errors, setErrors] = useState<Record<string, string>>({});

  const validateConfig = (): boolean => {
    const newErrors: Record<string, string> = {};
    
    if (!config.role.trim()) {
      newErrors.role = 'Role is required';
    }
    
    if (!config.company.trim()) {
      newErrors.company = 'Company is required';
    }
    
    if (config.duration < 15 || config.duration > 120) {
      newErrors.duration = 'Duration must be between 15 and 120 minutes';
    }
    
    if (config.focusAreas.length === 0) {
      newErrors.focusAreas = 'Please select at least one focus area';
    }
    
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    
    if (validateConfig()) {
      onStartInterview(config);
    }
  };

  const handleTemplateSelect = (template: typeof INTERVIEW_TEMPLATES[0]) => {
    setConfig({
      role: template.role,
      company: template.company,
      duration: template.duration,
      difficulty: template.difficulty,
      interviewType: template.interviewType,
      focusAreas: template.focusAreas,
      aiPersonality: template.aiPersonality
    });
    setErrors({});
  };

  const handleFocusAreaToggle = (area: string) => {
    setConfig(prev => ({
      ...prev,
      focusAreas: prev.focusAreas.includes(area)
        ? prev.focusAreas.filter(a => a !== area)
        : [...prev.focusAreas, area]
    }));
  };

  return (
    <div className="max-w-4xl mx-auto p-6 space-y-6">
      <div className="text-center">
        <h1 className="text-3xl font-bold text-gray-900 mb-2">
          Configure Your Interview
        </h1>
        <p className="text-gray-600">
          Set up your mock interview parameters for a personalized experience
        </p>
      </div>

      {/* Quick Templates */}
      <Card className="p-6">
        <h2 className="text-xl font-semibold mb-4">Quick Start Templates</h2>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          {INTERVIEW_TEMPLATES.map((template, index) => (
            <div
              key={index}
              className="p-4 border border-gray-200 rounded-lg cursor-pointer hover:border-blue-500 hover:bg-blue-50 transition-colors"
              onClick={() => handleTemplateSelect(template)}
            >
              <h3 className="font-medium text-gray-900 mb-2">{template.name}</h3>
              <p className="text-sm text-gray-600 mb-2">
                {template.duration} min • {template.difficulty} • {template.interviewType}
              </p>
              <div className="flex flex-wrap gap-1">
                {template.focusAreas.slice(0, 2).map(area => (
                  <span
                    key={area}
                    className="px-2 py-1 bg-gray-100 text-gray-700 text-xs rounded"
                  >
                    {area}
                  </span>
                ))}
                {template.focusAreas.length > 2 && (
                  <span className="px-2 py-1 bg-gray-100 text-gray-700 text-xs rounded">
                    +{template.focusAreas.length - 2} more
                  </span>
                )}
              </div>
            </div>
          ))}
        </div>
      </Card>

      {/* Custom Configuration */}
      <Card className="p-6">
        <h2 className="text-xl font-semibold mb-4">Custom Configuration</h2>
        
        <form onSubmit={handleSubmit} className="space-y-6">
          {/* Basic Info */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <Input
                label="Role/Position"
                value={config.role}
                onChange={(e) => setConfig(prev => ({ ...prev, role: e.target.value }))}
                placeholder="e.g., Software Engineer, Product Manager"
                error={errors.role}
                required
              />
            </div>
            <div>
              <Input
                label="Company"
                value={config.company}
                onChange={(e) => setConfig(prev => ({ ...prev, company: e.target.value }))}
                placeholder="e.g., Google, Startup, Enterprise"
                error={errors.company}
                required
              />
            </div>
          </div>

          {/* Interview Settings */}
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Duration (minutes)
              </label>
              <Input
                type="number"
                min="15"
                max="120"
                value={config.duration}
                onChange={(e) => setConfig(prev => ({ ...prev, duration: parseInt(e.target.value) }))}
                error={errors.duration}
              />
            </div>
            
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Difficulty Level
              </label>
              <select
                value={config.difficulty}
                onChange={(e) => setConfig(prev => ({ ...prev, difficulty: e.target.value as any }))}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              >
                <option value="beginner">Beginner</option>
                <option value="intermediate">Intermediate</option>
                <option value="advanced">Advanced</option>
              </select>
            </div>
            
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Interview Type
              </label>
              <select
                value={config.interviewType}
                onChange={(e) => setConfig(prev => ({ ...prev, interviewType: e.target.value as any }))}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              >
                <option value="behavioral">Behavioral</option>
                <option value="technical">Technical</option>
                <option value="mixed">Mixed</option>
              </select>
            </div>
          </div>

          {/* AI Personality */}
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              AI Interviewer Personality
            </label>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
              {(['friendly', 'professional', 'challenging'] as const).map(personality => (
                <label
                  key={personality}
                  className={`flex items-center p-3 border rounded-lg cursor-pointer transition-colors ${
                    config.aiPersonality === personality
                      ? 'border-blue-500 bg-blue-50'
                      : 'border-gray-200 hover:border-gray-300'
                  }`}
                >
                  <input
                    type="radio"
                    name="aiPersonality"
                    value={personality}
                    checked={config.aiPersonality === personality}
                    onChange={(e) => setConfig(prev => ({ ...prev, aiPersonality: e.target.value as any }))}
                    className="sr-only"
                  />
                  <div>
                    <div className="font-medium capitalize">{personality}</div>
                    <div className="text-sm text-gray-600">
                      {personality === 'friendly' && 'Supportive and encouraging approach'}
                      {personality === 'professional' && 'Standard corporate interview style'}
                      {personality === 'challenging' && 'Rigorous and demanding questions'}
                    </div>
                  </div>
                </label>
              ))}
            </div>
          </div>

          {/* Focus Areas */}
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Focus Areas
            </label>
            {errors.focusAreas && (
              <Alert variant="error" className="mb-3">
                {errors.focusAreas}
              </Alert>
            )}
            <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-5 gap-2">
              {FOCUS_AREAS.map(area => (
                <label
                  key={area}
                  className={`flex items-center p-2 border rounded cursor-pointer transition-colors ${
                    config.focusAreas.includes(area)
                      ? 'border-blue-500 bg-blue-50 text-blue-700'
                      : 'border-gray-200 hover:border-gray-300'
                  }`}
                >
                  <input
                    type="checkbox"
                    checked={config.focusAreas.includes(area)}
                    onChange={() => handleFocusAreaToggle(area)}
                    className="sr-only"
                  />
                  <span className="text-sm">{area}</span>
                </label>
              ))}
            </div>
          </div>

          {/* Submit Button */}
          <div className="flex justify-end">
            <Button
              type="submit"
              loading={loading}
              className="px-8 py-3"
            >
              Start Interview
            </Button>
          </div>
        </form>
      </Card>
    </div>
  );
}