'use client';

import { useState } from 'react';
import { AppLayout } from '@/components/layout/AppLayout';
import { InterviewConfig, InterviewConfiguration } from '@/components/interview/InterviewConfig';
import { InterviewInterface, InterviewSessionData } from '@/components/interview/InterviewInterface';
import { InterviewResults } from '@/components/interview/InterviewResults';
import { useAuth } from '@/components/auth/SessionManager';
import { Alert } from '@/components/ui/Alert';

type InterviewState = 'config' | 'interview' | 'results';

export default function InterviewPage() {
  const { user, isAuthenticated } = useAuth();
  const [state, setState] = useState<InterviewState>('config');
  const [currentConfig, setCurrentConfig] = useState<InterviewConfiguration | null>(null);
  const [sessionData, setSessionData] = useState<InterviewSessionData | null>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  // Check subscription limits
  const canStartInterview = user?.subscription?.usage?.sessionsUsed !== undefined && 
                           user?.subscription?.usage?.sessionsLimit !== undefined &&
                           user.subscription.usage.sessionsUsed < user.subscription.usage.sessionsLimit;

  const handleStartInterview = async (config: InterviewConfiguration) => {
    if (!canStartInterview) {
      setError('You have reached your interview limit for this billing period. Please upgrade your plan to continue.');
      return;
    }

    setLoading(true);
    setError(null);
    
    try {
      // Simulate API call to create interview session
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      setCurrentConfig(config);
      setState('interview');
    } catch (err) {
      setError('Failed to start interview. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  const handleEndInterview = (data: InterviewSessionData) => {
    setSessionData(data);
    setState('results');
    
    // Update usage count (in real app, this would be handled by the API)
    if (user?.subscription?.usage) {
      user.subscription.usage.sessionsUsed += 1;
    }
  };

  const handleStartNewInterview = () => {
    setCurrentConfig(null);
    setSessionData(null);
    setError(null);
    setState('config');
  };

  const handleViewDetailedReport = () => {
    // In a real app, this would navigate to a detailed report page
    console.log('Navigate to detailed report:', sessionData);
  };

  if (!isAuthenticated) {
    return (
      <AppLayout>
        <div className="flex items-center justify-center min-h-screen">
          <Alert variant="warning" title="Authentication Required">
            Please sign in to access the interview practice feature.
          </Alert>
        </div>
      </AppLayout>
    );
  }

  return (
    <AppLayout>
      <div className="min-h-screen bg-gray-50">
        {error && (
          <div className="p-4">
            <Alert variant="error" title="Error">
              {error}
            </Alert>
          </div>
        )}

        {state === 'config' && (
          <div className="py-8">
            {!canStartInterview && user && (
              <div className="max-w-4xl mx-auto px-6 mb-6">
                <Alert variant="warning" title="Interview Limit Reached">
                  You have used {user.subscription?.usage?.sessionsUsed || 0} of {user.subscription?.usage?.sessionsLimit || 0} interviews this month. 
                  Upgrade to premium for unlimited interviews.
                </Alert>
              </div>
            )}
            
            <InterviewConfig 
              onStartInterview={handleStartInterview}
              loading={loading}
            />
          </div>
        )}

        {state === 'interview' && currentConfig && (
          <InterviewInterface
            config={currentConfig}
            onEndInterview={handleEndInterview}
          />
        )}

        {state === 'results' && sessionData && (
          <div className="py-8">
            <InterviewResults
              sessionData={sessionData}
              onStartNewInterview={handleStartNewInterview}
              onViewDetailedReport={handleViewDetailedReport}
            />
          </div>
        )}
      </div>
    </AppLayout>
  );
}