'use client';

import { useState } from 'react';
import { Button } from '@/components/ui/Button';
import { Card } from '@/components/ui/Card';
import { InterviewSessionData } from './InterviewInterface';
import { 
  Download, 
  Share2, 
  BarChart3, 
  Clock, 
  MessageSquare, 
  TrendingUp,
  Star,
  AlertCircle,
  CheckCircle,
  ArrowRight
} from 'lucide-react';

interface InterviewResultsProps {
  sessionData: InterviewSessionData;
  onStartNewInterview: () => void;
  onViewDetailedReport: () => void;
}

interface PerformanceScore {
  overall: number;
  confidence: number;
  clarity: number;
  relevance: number;
  structure: number;
}

interface Recommendation {
  category: string;
  title: string;
  description: string;
  priority: 'high' | 'medium' | 'low';
}

export function InterviewResults({ 
  sessionData, 
  onStartNewInterview, 
  onViewDetailedReport 
}: InterviewResultsProps) {
  const [activeTab, setActiveTab] = useState<'overview' | 'transcript' | 'recommendations'>('overview');

  // Calculate performance scores (mock implementation)
  const performanceScore: PerformanceScore = {
    overall: 78,
    confidence: 75,
    clarity: 82,
    relevance: 80,
    structure: 74
  };

  // Generate recommendations (mock implementation)
  const recommendations: Recommendation[] = [
    {
      category: 'Communication',
      title: 'Reduce filler words',
      description: 'You used "um" and "uh" 12 times. Practice speaking more deliberately to sound more confident.',
      priority: 'high'
    },
    {
      category: 'Structure',
      title: 'Use the STAR method',
      description: 'Structure your behavioral answers using Situation, Task, Action, Result for clearer responses.',
      priority: 'high'
    },
    {
      category: 'Confidence',
      title: 'Maintain eye contact',
      description: 'You looked away from the camera 8 times. Practice maintaining steady eye contact.',
      priority: 'medium'
    },
    {
      category: 'Content',
      title: 'Provide specific examples',
      description: 'Include more quantifiable achievements and specific project details in your answers.',
      priority: 'medium'
    }
  ];

  const formatDuration = (start: Date, end: Date): string => {
    const duration = Math.floor((end.getTime() - start.getTime()) / 1000 / 60);
    return `${duration} minutes`;
  };

  const getScoreColor = (score: number): string => {
    if (score >= 80) return 'text-green-600';
    if (score >= 60) return 'text-yellow-600';
    return 'text-red-600';
  };

  const getScoreBackground = (score: number): string => {
    if (score >= 80) return 'bg-green-100';
    if (score >= 60) return 'bg-yellow-100';
    return 'bg-red-100';
  };

  const getPriorityIcon = (priority: string) => {
    switch (priority) {
      case 'high':
        return <AlertCircle className="w-4 h-4 text-red-500" />;
      case 'medium':
        return <Star className="w-4 h-4 text-yellow-500" />;
      case 'low':
        return <CheckCircle className="w-4 h-4 text-green-500" />;
      default:
        return null;
    }
  };

  const handleExport = () => {
    // Mock export functionality
    const exportData = {
      session: sessionData,
      performance: performanceScore,
      recommendations
    };
    
    const blob = new Blob([JSON.stringify(exportData, null, 2)], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `interview-results-${sessionData.sessionId}.json`;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  };

  const handleShare = () => {
    // Mock share functionality
    if (navigator.share) {
      navigator.share({
        title: 'My Interview Results',
        text: `I just completed a mock interview for ${sessionData.config.role} and scored ${performanceScore.overall}%!`,
        url: window.location.href
      });
    } else {
      // Fallback: copy to clipboard
      navigator.clipboard.writeText(
        `I just completed a mock interview for ${sessionData.config.role} and scored ${performanceScore.overall}%! Check out the AI Mock Interview Platform.`
      );
    }
  };

  return (
    <div className="max-w-6xl mx-auto p-6 space-y-6">
      {/* Header */}
      <div className="text-center">
        <div className="inline-flex items-center justify-center w-16 h-16 bg-green-100 rounded-full mb-4">
          <CheckCircle className="w-8 h-8 text-green-600" />
        </div>
        <h1 className="text-3xl font-bold text-gray-900 mb-2">
          Interview Complete!
        </h1>
        <p className="text-gray-600">
          Here's your performance summary and personalized feedback
        </p>
      </div>

      {/* Quick Stats */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
        <Card className="p-4 text-center">
          <div className={`text-3xl font-bold mb-1 ${getScoreColor(performanceScore.overall)}`}>
            {performanceScore.overall}%
          </div>
          <div className="text-sm text-gray-600">Overall Score</div>
        </Card>
        
        <Card className="p-4 text-center">
          <div className="text-3xl font-bold text-gray-900 mb-1">
            {sessionData.responses.length}
          </div>
          <div className="text-sm text-gray-600">Questions Answered</div>
        </Card>
        
        <Card className="p-4 text-center">
          <div className="text-3xl font-bold text-gray-900 mb-1">
            {formatDuration(sessionData.startTime, sessionData.endTime)}
          </div>
          <div className="text-sm text-gray-600">Duration</div>
        </Card>
        
        <Card className="p-4 text-center">
          <div className="text-3xl font-bold text-gray-900 mb-1">
            {recommendations.filter(r => r.priority === 'high').length}
          </div>
          <div className="text-sm text-gray-600">Key Areas</div>
        </Card>
      </div>

      {/* Action Buttons */}
      <div className="flex flex-wrap gap-4 justify-center">
        <Button onClick={onViewDetailedReport} className="flex items-center">
          <BarChart3 className="w-4 h-4 mr-2" />
          View Detailed Report
        </Button>
        
        <Button variant="secondary" onClick={handleExport} className="flex items-center">
          <Download className="w-4 h-4 mr-2" />
          Export Results
        </Button>
        
        <Button variant="secondary" onClick={handleShare} className="flex items-center">
          <Share2 className="w-4 h-4 mr-2" />
          Share Results
        </Button>
        
        <Button variant="primary" onClick={onStartNewInterview} className="flex items-center">
          <ArrowRight className="w-4 h-4 mr-2" />
          Start New Interview
        </Button>
      </div>

      {/* Tabs */}
      <div className="border-b border-gray-200">
        <nav className="-mb-px flex space-x-8">
          {[
            { id: 'overview', label: 'Performance Overview', icon: BarChart3 },
            { id: 'transcript', label: 'Interview Transcript', icon: MessageSquare },
            { id: 'recommendations', label: 'Recommendations', icon: TrendingUp }
          ].map(tab => (
            <button
              key={tab.id}
              onClick={() => setActiveTab(tab.id as any)}
              className={`flex items-center py-2 px-1 border-b-2 font-medium text-sm ${
                activeTab === tab.id
                  ? 'border-blue-500 text-blue-600'
                  : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
              }`}
            >
              <tab.icon className="w-4 h-4 mr-2" />
              {tab.label}
            </button>
          ))}
        </nav>
      </div>

      {/* Tab Content */}
      {activeTab === 'overview' && (
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          {/* Performance Breakdown */}
          <Card className="p-6">
            <h3 className="text-lg font-semibold mb-4">Performance Breakdown</h3>
            <div className="space-y-4">
              {Object.entries(performanceScore).map(([key, score]) => {
                if (key === 'overall') return null;
                return (
                  <div key={key} className="flex items-center justify-between">
                    <span className="capitalize font-medium">{key}</span>
                    <div className="flex items-center">
                      <div className="w-32 bg-gray-200 rounded-full h-2 mr-3">
                        <div
                          className={`h-2 rounded-full ${
                            score >= 80 ? 'bg-green-500' : score >= 60 ? 'bg-yellow-500' : 'bg-red-500'
                          }`}
                          style={{ width: `${score}%` }}
                        ></div>
                      </div>
                      <span className={`font-semibold ${getScoreColor(score)}`}>
                        {score}%
                      </span>
                    </div>
                  </div>
                );
              })}
            </div>
          </Card>

          {/* Session Details */}
          <Card className="p-6">
            <h3 className="text-lg font-semibold mb-4">Session Details</h3>
            <div className="space-y-3">
              <div className="flex justify-between">
                <span className="text-gray-600">Role</span>
                <span className="font-medium">{sessionData.config.role}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-gray-600">Company</span>
                <span className="font-medium">{sessionData.config.company}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-gray-600">Interview Type</span>
                <span className="font-medium capitalize">{sessionData.config.interviewType}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-gray-600">Difficulty</span>
                <span className="font-medium capitalize">{sessionData.config.difficulty}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-gray-600">AI Personality</span>
                <span className="font-medium capitalize">{sessionData.config.aiPersonality}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-gray-600">Start Time</span>
                <span className="font-medium">{sessionData.startTime.toLocaleString()}</span>
              </div>
            </div>
          </Card>
        </div>
      )}

      {activeTab === 'transcript' && (
        <Card className="p-6">
          <h3 className="text-lg font-semibold mb-4">Complete Interview Transcript</h3>
          <div className="space-y-4 max-h-96 overflow-y-auto">
            {sessionData.transcript.map(entry => (
              <div
                key={entry.id}
                className={`p-4 rounded-lg ${
                  entry.speaker === 'interviewer'
                    ? 'bg-blue-50 border-l-4 border-blue-500'
                    : 'bg-green-50 border-l-4 border-green-500'
                }`}
              >
                <div className="flex justify-between items-start mb-2">
                  <span className="font-medium">
                    {entry.speaker === 'interviewer' ? 'AI Interviewer' : 'You'}
                  </span>
                  <span className="text-sm text-gray-500">
                    {entry.timestamp.toLocaleTimeString()}
                  </span>
                </div>
                <p className="text-gray-800">{entry.text}</p>
                {entry.confidence && (
                  <div className="mt-2 text-xs text-gray-500">
                    Confidence: {Math.round(entry.confidence * 100)}%
                  </div>
                )}
              </div>
            ))}
          </div>
        </Card>
      )}

      {activeTab === 'recommendations' && (
        <div className="space-y-4">
          <Card className="p-6">
            <h3 className="text-lg font-semibold mb-4">Personalized Improvement Plan</h3>
            <p className="text-gray-600 mb-6">
              Based on your interview performance, here are specific areas to focus on for improvement:
            </p>
            
            <div className="space-y-4">
              {recommendations.map((rec, index) => (
                <div
                  key={index}
                  className={`p-4 rounded-lg border-l-4 ${
                    rec.priority === 'high'
                      ? 'bg-red-50 border-red-500'
                      : rec.priority === 'medium'
                      ? 'bg-yellow-50 border-yellow-500'
                      : 'bg-green-50 border-green-500'
                  }`}
                >
                  <div className="flex items-start justify-between mb-2">
                    <div className="flex items-center">
                      {getPriorityIcon(rec.priority)}
                      <span className="ml-2 font-medium">{rec.title}</span>
                    </div>
                    <span className={`px-2 py-1 rounded text-xs font-medium ${getScoreBackground(
                      rec.priority === 'high' ? 40 : rec.priority === 'medium' ? 70 : 90
                    )}`}>
                      {rec.category}
                    </span>
                  </div>
                  <p className="text-gray-700">{rec.description}</p>
                </div>
              ))}
            </div>
          </Card>

          <Card className="p-6">
            <h3 className="text-lg font-semibold mb-4">Next Steps</h3>
            <div className="space-y-3">
              <div className="flex items-center">
                <CheckCircle className="w-5 h-5 text-green-500 mr-3" />
                <span>Practice the STAR method for behavioral questions</span>
              </div>
              <div className="flex items-center">
                <CheckCircle className="w-5 h-5 text-green-500 mr-3" />
                <span>Record yourself answering common questions</span>
              </div>
              <div className="flex items-center">
                <CheckCircle className="w-5 h-5 text-green-500 mr-3" />
                <span>Take another mock interview in 1-2 weeks</span>
              </div>
              <div className="flex items-center">
                <CheckCircle className="w-5 h-5 text-green-500 mr-3" />
                <span>Focus on reducing filler words in daily conversations</span>
              </div>
            </div>
          </Card>
        </div>
      )}
    </div>
  );
}